import {library} from '@fortawesome/fontawesome-svg-core'
import {faGoogle} from '@fortawesome/free-brands-svg-icons'
import {faFacebookF} from '@fortawesome/free-brands-svg-icons'

import {required} from "vuelidate/lib/validators";
import jwt_decode from "jwt-decode";

// import {required, numeric, minLength, maxLength} from "vuelidate/lib/validators";


library.add(faGoogle)
library.add(faFacebookF)

export default {
  middleware: 'auth',
  auth: 'guest',
  name: 'Login',
  components: {},
  data() {
    return {
      busy:false ,
      loginForm: {
        email: '',
        password: '',
        token: ""
      },
      errors: null,
    }
  },
  validations: {
    loginForm: {
      email: {
        required,
        serverError(value) {
          if (value === '') return true
          return !(this.errors != null && this.errors.email && value == this.errors.email.value)
        }
      },
      password: {
        required,
      },
    }
  },
  head() {
    return {
      title: 'Sign In',
      meta: [
        {
          name: 'description',
          content: 'sign in with Google or Facebook or can create an account'
        },
        {
          name: 'keywords',
          content: 'sign in, login, google, facebook, account'
        }
      ]
    }
  },
  async mounted() {
    try {
      await this.$recaptcha.init()
    } catch (e) {
      console.log(e);
    }

  },
  methods: {

    onReset(evt) {
      evt.preventDefault()
      // Reset our loginForm values
      this.loginForm.email = ''
      this.loginForm.password = ''

    },

    async onSubmit(evt) {
      const self = this
      this.$v.$reset()
      this.errors = null
      evt.preventDefault()
      this.$v.loginForm.$touch()
      if (this.$v.loginForm.$invalid) {
        console.log("vuelidate error")
      } else {
        try {
          this.busy = true
          let token = await this.$recaptcha.execute('login')
          self.loginForm.token = token;
          let response = await self.$auth.loginWith('local', {data: self.loginForm})
          await self.$auth.setUserToken(response.data.token)
          let decoded = jwt_decode(response.data.token);
          self.$auth.setUser(decoded)
          this.busy = false
          self.$router.push('/')
        } catch (error) {
          this.busy = false
          if(error.response !== null)
            if (error.response.data.errors) {
              // console.log(JSON.stringify(error.response.data.errors))
              self.errors = error.response.data.errors
            }
          console.log(error)
        }

      }
    }
  },

  beforeDestroy() {
    this.$recaptcha.destroy()
  }

}
